/* Banner */
body.is-preload #banner .logo {
  -moz-transform: none;
  -webkit-transform: none;
  -ms-transform: none;
  transform: none;
  opacity: 1;
}
body.is-preload #banner h2 {
  opacity: 1;
  -moz-transform: none;
  -webkit-transform: none;
  -ms-transform: none;
  transform: none;
  -moz-filter: none;
  -webkit-filter: none;
  -ms-filter: none;
