}
body.is-preload #banner p {
  opacity: 1;
  -moz-transform: none;
  -webkit-transform: none;
  -ms-transform: none;
  transform: none;
  -moz-filter: none;
  -webkit-filter: none;
  -ms-filter: none;
  filter: none;
}

